.promo-yoga__logo {
  letter-spacing: 0.26em;
  font-size: 67px;
  font-weight: bold;
  line-height: 0px;
  color: #ffffff;
  text-shadow: 0px 4px 50px rgba(131, 115, 161, 0.6);
  padding-top: 17px;
  padding-bottom: 27px;
  text-align: center;
}

.promo-yoga__action {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  margin-top: 15px;
  margin-right: 20px;
  margin-left: 50px;
  color: @primary;

  p {
    font-size: 10px;
    line-height: 12px;
    margin: 0;
    margin-left: 7px;
  }
}

.promo-yoga__button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 26px;
  background-color: @primary;

  svg {
    transform: rotate(90deg);
    fill: #ffffff;
    margin-left: 2px;
  }
}

.promo-yoga__slogan {
  display: flex;
  align-items: center;
  padding-bottom: 10px;
  font-family: Caveat, "Open Sans", sans-serif;
  font-size: 18px;
}

.promo-yoga {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 40px 24px 55px 44px;
}

@media (min-width: @tablet-width) {
  .promo-yoga {
    padding: 62px 24px 55px 44px;
  }
  .promo-yoga__slogan {
    font-size: 24px;

    margin: 0 auto;
    margin-bottom: 55px;
    margin-left: 118px;
  }

  .promo-yoga__logo {
    font-size: 140px;
    font-weight: bold;
    letter-spacing: 0.34em;
    margin-left: 24px;
  }

  .promo-yoga__action {
    margin: 47px auto 0 auto;
    max-width: 200px;
  }
}

@media (min-width: @desktop-width) {
  .promo-yoga {
    padding: 107px 24px 55px 132px;
  }
  .promo-yoga__slogan {
    font-size: 45px;
    margin: 0 auto 74px 160px;
  }

  .promo-yoga__logo {
    font-size: 265px;
    padding-top: 98px;
  }

  .promo-yoga__action {
    margin: 113px auto 0;
    max-width: 312px;

    p {
      font-size: 14px;
      line-height: 20px;
    }
  }

  .promo-yoga__button {
    width: 74px;
    height: 52px;
  }
}
