.main-navigation {
  font-size: 28px;
  line-height: 38px;
  display: -webkit-box;
  display: flex;
  padding: 0;
  margin: 0;
}
.main-navigation--opened .main-navigation__dropdown {
  visibility: visible;
  opacity: 1;
}
.main-navigation--opened .main-navigation__toggle {
  background-color: #8e80a9;
}
.main-navigation--opened .main-navigation__toggle::before,
.main-navigation--opened .main-navigation__toggle::after {
  content: "";
  top: 10px;
  left: -3px;
  width: 27px;
  background-color: #e6c8f5;
}
.main-navigation--opened .main-navigation__toggle::before {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  box-shadow: none;
}
.main-navigation--opened .main-navigation__toggle::after {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}
.main-navigation__toggle {
  position: absolute;
  top: 16px;
  right: 20px;
  cursor: pointer;
  display: block;
  padding: 0;
  width: 22px;
  height: 22px;
  background-color: transparent;
  border: 0;
  z-index: 15;
  -webkit-transition: all 250ms ease-in-out;
  transition: all 250ms ease-in-out;
}
.main-navigation__toggle::before,
.main-navigation__toggle::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 4px;
  background-color: #8e80a9;
  -webkit-transition: all 250ms ease-in-out;
  transition: all 250ms ease-in-out;
}
.main-navigation__toggle::before {
  top: 0;
  box-shadow: 0 9px 0 0 #8e80a9;
}
.main-navigation__toggle::after {
  top: 100%;
  -webkit-transform: translateY(-100%);
          transform: translateY(-100%);
}
.main-navigation__phone-number {
  font-size: 12px;
  margin-left: 20px;
  margin-top: 5px;
  color: #8e80a9;
}
.main-navigation__dropdown {
  display: -webkit-box;
  display: flex;
  visibility: hidden;
  -webkit-box-pack: center;
          justify-content: center;
  -webkit-box-align: center;
          align-items: center;
  background-color: #8e80a9;
  position: fixed;
  width: 100%;
  height: 100%;
  padding-top: 5px;
  z-index: 10;
  text-align: center;
  -webkit-transition: visibility 0s, opacity 0.25s linear;
  transition: visibility 0s, opacity 0.25s linear;
  opacity: 0;
}
.main-navigation__dropdown a {
  color: #e6c8f5;
}
.main-navigation__dropdown a:hover,
.main-navigation__dropdown a:focus {
  color: #2f2b32;
}
@media (min-width: 1440px) {
  .main-navigation__phone-number {
    font-size: 16px;
    margin-left: 28px;
    margin-top: 10px;
  }
  .main-navigation__toggle {
    top: 20px;
    right: 27px;
  }
}
